import React from "react";
import styled from "styled-components";
import Slider from "react-slick";

import bg from "../public/bgHeader.png";
import borders from "../public/borders.png";

import startuprr from "../public/icons/start.png";
import NEXT from "../public/icons/NEXT.png";
import apex from "../public/icons/apex.png";
import FOOD from "../public/icons/FOOD.png";
import multiTodo from "../public/icons/multiTodo.png";
import netflix from "../public/icons/netflix.png";
import chat from "../public/icons/chat.png";
import auth from "../public/icons/auth.png";
import rock from "../public/icons/rock.png";
import tetris from "../public/icons/tetris.png";
import dnd from "../public/icons/dnd.png";
import pok from "../public/icons/pok.png";
import code from "../public/icons/code.png";
import merk from "../public/icons/merk.png";
import git from "../public/icons/git.png";
import CountDown from "../public/icons/CountDown.png";
import ribbon from "../public/Ribbon.png";

export const slides = [
  {
    img: startuprr,
    href: "https://automedon.github.io/Startuprr/",
    name: "Startuprr"
  },
  {
    img: NEXT,
    href: "https://resort-with-nextjs.inzhenerintellect.now.sh/",
    name: "Hotel Rooms"
  },
  {
    img: apex,
    href: "https://dry-crag-95171.herokuapp.com/",
    name: "Apex tracker"
  },
  {
    img: tetris,
    href: "https://automedon.github.io/Tetris/",
    name: "Tetris"
  },

  {
    img: multiTodo,
    href: "https://automedon.github.io/MultiTodo/",
    name: "MultiTodo"
  },
  {
    img: netflix,
    href: "https://netflix-clone.inzhenerintellect.now.sh/",
    name: "Netflix"
  },
  {
    img: auth,
    href: "https://aqueous-river-29369.herokuapp.com/",
    name: "Authorization"
  },
  {
    img: chat,
    href: "https://automedon.github.io/simpleChat/",
    name: "Chat"
  },
  {
    img: rock,
    href: "https://automedon.github.io/rockpaperscissors/",
    name: "RockPaper"
  },
  {
    img: FOOD,
    href: "https://automedon.github.io/Startuprr/",
    name: "Recipes"
  },
  {
    img: dnd,
    href: "https://automedon.github.io/HugeDnDList/",
    name: "Huge DND List"
  },
  { img: pok, href: "https://automedon.github.io/Pokedx/", name: "Pokedx" },
  {
    img: code,
    href: "https://github.com/Automedon/ExampleOfCode",
    name: "Code"
  },
  {
    img: CountDown,
    href: "https://automedon.github.io/CountDown/",
    name: "CountDown"
  },
  { img: merk, href: "https://automedon.github.io/Merkury/", name: "Merkury" }
];
const Ribbon = styled.img`
  width: 100%;
  height: 100%;
  position: relative;
  top: 5px;
  object-fit: cover;
  object-position: 0 0;
`;
const Wrapper = styled.section`
  margin-top: 5%;
  margin-bottom: 5%;
  background: url(${bg}) no-repeat top;
  font-family: Scribble, "Segoe UI", serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;

  div.textWrapper {
    color: #51504c;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    @media (max-width: 1000px) {
      flex-direction: column;
    }
    div.text {
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: stretch;
      h1.Welcome {
        font-size: 100px;
        @media (max-width: 500px) {
          font-size: 60px;
        }
      }
      h1.My {
        font-size: 75px;
        @media (max-width: 500px) {
          font-size: 30px;
        }
      }
      h1.Automedon {
        font-size: 60px;
        @media (max-width: 500px) {
          font-size: 40px;
        }
      }
      h1.AND {
        font-size: 30px;
        @media (max-width: 500px) {
          font-size: 40px;
        }
      }
      h1.Frontend {
        font-size: 60px;
        @media (max-width: 500px) {
          font-size: 40px;
        }
      }
      h1.Developer {
        font-size: 60px;
        @media (max-width: 500px) {
          font-size: 40px;
        }
      }
    }
    div.image {
      width: 449px;
      height: 449px;
      background: url(${borders}) no-repeat center center/cover;
      @media (max-width: 500px) {
        background: none;
        width: 90%;
        text-align: center;
        height: auto;
      }
      div.slider {
        margin-left: 19px;
        margin-top: 97px;
        width: 385px;
        height: 247px;

        @media (max-width: 500px) {
          width: 85%;
          margin: 20px auto;
        }
        *::before {
          color: #888783;
        }

        div.slide {
          img {
            width: 385px;
            height: 247px;
            border-radius: 5px;
            @media (max-width: 500px) {
              width: 100%;
              height: 180px;
            }
          }
        }
      }
    }
  }
`;
const settings = {
  infinite: true,
  speed: 500,
  slidesToShow: 1,
  slidesToScroll: 1,
  accessibility: true,
  arrows: true,
  autoplay: true,
  autoplaySpeed: 3000
};

const slider = (
  <div className="slider">
    <Slider {...settings}>
      {slides.map(({ img, href }) => (
        <div className="slide" key={img + href}>
          <a href={href}>
            <img src={img} alt="" />
          </a>
        </div>
      ))}
    </Slider>
  </div>
);
const Header = () => {
  return (
    <>
      <Wrapper>
        <div className="textWrapper" id="HOME">
          <div className="text">
            <h1 className="Welcome">Welcome</h1>
            <h1 className="My">My name is</h1>
            <h1 className="Automedon">Automedon</h1>
            <h1 className="AND">AND I'm</h1>
            <h1 className="Frontend">Frontend</h1>
            <h1 className="Developer">Developer</h1>
          </div>
          <div className="image">{slider}</div>
        </div>
      </Wrapper>
      <Ribbon src={ribbon} alt="" />
    </>
  );
};

export default Header;
