import React, { useState } from "react";
import styled from "styled-components";
import { slides } from "./Header";
import Port from "../public/portfolio.png";
import Stars from "../public/stars.png";
import ReverseRibbon from "../public/ReverseRibbon.png";

const Wrapper = styled.section`
  background-color: #231d1c;
  font-family: Scribble, "Segoe UI", serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  img.Ribbon {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 0 0;
  }
  img.ReverseRibbon {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 0 0;
    position: relative;
    top: 2px;
  }
  div.bg {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 200px;
    margin-bottom: 30px;
    @media (max-width: 1000px) {
      padding: 2% 0;
      justify-content: start;
      height: 150px;
      margin: 0;
    }
    @media (max-width: 700px) {
      padding: 2% 0;
      height: 100px;
      margin: 0;
      justify-content: start;
    }

    img {
      max-width: 400px;
      height: 100%;
      @media (max-width: 1000px) {
        transform: rotate(15deg);
        max-width: 400px;
        height: 200px;
      }
      @media (max-width: 700px) {
        transform: rotate(15deg);
        max-width: 280px;
        height: 100px;
      }
    }

    img.Stars {
      position: relative;
      top: 100px;
      z-index: 0;
      width: unset;
      height: unset;
      @media (max-width: 1000px) {
        display: none;
      }
    }
  }
  div.grid {
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @media (max-width: 700px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 500px) {
      grid-template-columns: repeat(1, 1fr);
    }
    grid-gap: 5px;
    width: 100%;
    div.oneGrid {
      width: auto;
      background-color: #888888;
      h1 {
        text-align: center;
      }
      div.img {
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
  }
  div.button {
    margin: 30px 0;
    font-family: Scribble, "Segoe UI", serif;
    font-size: 60px;
    border-radius: 6px;
    border-width: 0;
    background-color: #d2d0cc;
    color: black;
    padding: 0 20px;
    border-color: black !important;
    align-self: center;
    cursor: pointer;
    a {
      color: unset !important;
    }
    @media (max-width: 700px) {
      width: 80%;
      padding: 0 10px;
      font-size: 40px;
    }
  }
`;

const Portfolio = () => {
  const [view, setView] = useState(false);
  const Grid = (
    <div className="grid">
      {slides.slice(0, !view ? 6 : slides.length).map(({ name, img, href }) => {
        return (
          <div className="oneGrid" key={name + img + href}>
            <h1>{name}</h1>
            <div className="img">
              <a href={href}>
                <img src={img} alt={name} />
              </a>
            </div>
          </div>
        );
      })}
    </div>
  );
  return (
    <Wrapper>
      <div className="bg" id="PORTFOLIO">
        <img src={Port} alt="Port" />

        <img className="Stars" src={Stars} alt="Stars" />
      </div>
      {Grid}
      <div className="button" onClick={() => setView(!view)}>
        {!view ? (
          <a href={"#ABOUT"}>View more</a>
        ) : (
          <a href={"#PORTFOLIO"}>Close</a>
        )}
      </div>
      <img className="ReverseRibbon" src={ReverseRibbon} alt="" />
    </Wrapper>
  );
};

export default Portfolio;
