import React from "react";
import styled from "styled-components";
import contacts from "../public/contacts.png";
import ribbon from "../public/Ribbon.png";

const Wrapper = styled.section`
  width: 100%;
  background-color: #231d1c;
  display: flex;
  align-items: center;
  font-family: Scribble, "Segoe UI", serif;
  padding-top: 3%;
  height: 210px;
  @media (max-width: 700px) {
    flex-direction: column;
    height: auto;
  }
  div.bg {
    width: 469px;
    height: 200px;
    @media (max-width: 700px) {
      width: 90%;
      height: 100px;
    }
    img {
      width: 100%;
      height: 100%;
      @media (max-width: 700px) {
        transform: rotate(15deg);
        width: 240px;
      }
    }
  }
  div.mail {
    text-align: center;
    h1 {
      font-size: 50px;
      @media (max-width: 700px) {
        font-size: 30px;
      }
    }
    h2 {
      font-size: 30px;
      @media (max-width: 700px) {
        font-size: 18px;
        padding-bottom: 50px;
      }
    }
  }
`;

const Ribbon = styled.img`
  width: 100%;
  height: 100%;
  position: relative;
  top: 5px;
  object-fit: cover;

  object-position: 0 0;
`;

const Contacts = () => {
  return (
    <>
      <Ribbon src={ribbon} />
      <Wrapper id="CONTACTS">
        <div className="bg">
          <img src={contacts} alt="contacts" />
        </div>
        <div className="mail">
          <h1>Gmail</h1>
          <h2>inzhener.intellect@gmail.com</h2>
        </div>
      </Wrapper>
    </>
  );
};

export default Contacts;
